body {
  font-family: 'formulaone';
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@font-face {
    font-family: 'formulaone';
    src: url('./Formula1-Regular.ttf');
}

.App-header {
  background-image: linear-gradient(to bottom right, #051c30, #074b86);
  font-family: 'formulaone';
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

